@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap");

body {
  margin: 0px;
  padding: 0px;

  background-color: #000000;
  font-family: "Montserrat", sans-serif;
}
* {
  box-sizing: border-box;
}
ul {
  list-style: none;
  padding-left: 0px;
}
a {
  text-decoration: none;
}
button {
  outline: none;
  border: none;
}
input {
  outline: none;
  border: none;
}
.logo {
  position: relative;
  right: 28%;
}
.logo img {
  width: 100px;
  height: auto;
  cursor: pointer;
  margin-top: 15px;
}

#main {
  width: 100%;
  height: 100vh;
  position: relative;
}
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.336);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 110px;
  z-index: 1;
  background-color: #000000;
}
nav ul {
  display: flex;
}
.active {
  background-color: #d0ff00;
  color: #000000;
  box-shadow: 5px 10px 30px rgba(189, 198, 64, 0.411);
  transition: all ease 0.2s;
}
nav ul li a {
  font-family: calibri;
  height: 40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 22px;
  display: flex;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 1px;
  border-radius: 3px;
  transition: 0.2s ease-in-out;
}
nav ul li a:hover {
  background-color: #d0ff00;
  color: #000000;
  box-shadow: 5px 10px 30px rgba(189, 198, 64, 0.411);
  transition: all ease 0.2s;
  transform: scale(0.9);
  cursor: pointer;
}
.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

nav .menu-btn,
.menu-icon {
  display: none;
}
.responsive {
  display: none;
}
.name {
  font-family: "Montserrat";
  width: 500px;
  position: absolute;
  left: 50%;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
}
.name span {
  color: #d0ff00;
  border-bottom: 2px solid #d0ff00;
}
.name .details {
  color: #dddddd;
  line-height: 25px;
}
.name h1 {
  font-family: "Montserrat";
  font-size: 46px;
  margin: 0px;
  letter-spacing: 3px;
  color: #ffffff;
}
.main-page-container {
  display: flex;
}
.surveyor-image-div {
  flex: 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.surveyor-image-div img {
  max-width: 100%;
  max-height: 100%;
  margin-right: 80px;
}
.construction-building-div {
  flex: 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.construction-building-div img {
  max-width: 60%;
  max-height: 60%;
  margin-left: 200px;
}
.header-btns {
  display: flex;
  justify-content: center;
  padding-top: 22px;
}
.cv-btn {
  font-size: 16px;
  width: 110px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d0ff00;
  box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.137);
  border-radius: 10px;
  color: #000266;
  cursor: pointer;
}
.cv-btn1 {
  font-size: 16px;
  width: 110px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
  border-radius: 10px;
  margin-left: 20px;
  cursor: pointer;
}
.cv-btn:hover {
  background-color: #ffffff;
  transition: all ease 0.5s;
  color: #000266;
}
.cv-btn1:hover {
  background-color: #ffffff;
  transition: all ease 0.5s;
  color: #000266;
}

.arrow {
  align-self: end;
  width: 50%;
  height: 15%;
  margin-bottom: 6em;
  position: absolute;
  bottom: 0px;
  border-right: 2px solid #d0ff00;
  animation: arrow-animation ease 1.5s;
}
.arrow::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 11px 11px 0px 11px;
  border-color: #d0ff00 transparent transparent transparent;
  right: -0.7em;
  bottom: -2px;
}

.spacer {
  height: 8vh;
  margin-top: -150px;
}

/***************SERVICES***************/

.services-container {
  margin-top: 82px;
  color: #fff;
  margin-left: 10%;
  margin-right: 10%;
}

.services-title {
  color: #fff;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-align: center;
}

.services {
  margin-top: 37px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100%;
}

.serviceCard-container {
  display: flex;
  flex-direction: column;
  padding: 18px 24px;
  max-width: 445px;
}

.serviceCard-container img {
  margin-bottom: 26px;
}

.serviceCard-title {
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  color: #d0ff00;
}

.serviceCard-description {
  color: #b4b4b4;
  margin-top: 6px;
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
}

/***************CONTACT******************/

.contact-container h1 {
  color: #ffffff;
  font-size: 2.5rem;
  margin: 0px;
  padding: 0px;
}

.contact-container h2 {
  color: #d0ff00;
  margin-bottom: 50px;
  text-decoration: underline;
}

.contact-container h3 {
  color: white;
  margin: 0 auto;
  max-width: 230px;
  text-align: left;
}

.contact-container p {
  color: rgba(247, 247, 247, 0.678);
  max-width: 35%;
  display: inline-block;
  text-align: center;
  font-size: 1.1rem;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 0px;
}

.contact-container {
  text-align: center;
  margin-top: 100px;
  margin-left: 10%;
  margin-right: 10%;
}

.bottom-spacer-div {
  height: 10vh;
}

/***************ICONS***************/
.icons {
  color: #d0ff00;
  margin-right: 10px;
  vertical-align: middle;
}

/***************RESPONSIVE***************/

@media (max-width: 520px) {
  .name {
    width: 80%;
  }
  .contact-container h1 {
    font-size: 2.2rem;
  }
  .serviceCard-container {
    padding: 0px;
    max-width: 100%;
  }
}

@media (min-width: 600px) and (max-width: 900px) {
  .contact-container p {
    max-width: 75%;
  }
}
@media (min-width: 901px) and (max-width: 1200px) {
  .contact-container p {
    max-width: 70%;
  }
}

@media (max-width: 1200px) {
  #main {
    background: none;
  }
  .responsive {
    display: initial;
    width: 100%;
    height: 300px;
    position: relative;
    overflow: hidden;
  }
  .responsive img {
    margin-top: 50px;
    width: 100%;
    height: 40%;
    object-fit: contain;
  }
  .surveyor-image-div {
    display: none;
  }
  .construction-building-div {
    display: none;
  }
  nav {
    display: flex;
    justify-content: space-between;
    box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.336);
    position: fixed;
    width: 100%;
    height: 65px;
    padding: 0px 15px;
    background-color: #000000;
  }
  .menu-icon {
    display: block;
  }
  .menu {
    display: none;
    position: absolute;
    top: 65px;
    left: 0px;
    background-color: #000000;
    border-bottom: 4px solid #d0ff00;
    width: 100%;
    padding: 0px;
    margin: 0px;
  }
  .menu li {
    width: 100%;
  }
  nav .menu li a {
    width: 100%;
    height: 40px;
    justify-content: center;
    align-items: center;
    margin: 0px;
    padding: 25px;
    border: 1px solid rgba(158, 155, 0, 0.03);
  }
  nav .menu-icon {
    cursor: pointer;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }
  nav .menu-icon .nav-icon {
    background-color: #d0ff00;
    position: absolute;
    display: block;
    height: 2px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 18px;
  }
  nav .menu-icon .nav-icon:before,
  nav .menu-icon .nav-icon:after {
    background: #d0ff00;
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    transition: all ease-out 0.2s;
    width: 100%;
  }
  nav .menu-icon .nav-icon:before {
    top: 5px;
  }
  nav .menu-icon .nav-icon:after {
    top: -5px;
  }
  nav .menu-btn:checked ~ .menu-icon .nav-icon {
    background: transparent;
  }
  nav .menu-btn:checked ~ .menu-icon .nav-icon:before {
    transform: rotate(-45deg);
    top: 0;
  }
  nav .menu-btn:checked ~ .menu-icon .nav-icon:after {
    transform: rotate(45deg);
    top: 0;
  }
  nav .menu-btn {
    display: none;
  }
  nav .menu-btn:checked ~ .menu {
    display: block;
  }
  .logo {
    left: 0%;
  }
  .logo img {
    width: 70px;
  }
  .name {
    top: 53%;
  }
  .name h1 {
    margin-top: 200px;
  }
  .header-btns {
    margin: 0;
  }
  .arrow {
    display: none;
  }
  .contact-container p {
    max-width: 445px;
  }
}
